import React from 'react';

import { prizes } from 'assets/data/table';

import styles from './Table.module.scss';

const Counter = ({ targetDate }) => {
  return (
    <div className={styles.wrapper}>
      <table>
        <tr>
          <th>Prize</th>
          <th>Coupon No.</th>
        </tr>
        {prizes.map((prize, index) => {
  let suffix;
  if (prize.prizeOrder === 1) {
    suffix = "st";
  } else if (prize.prizeOrder === 2) {
    suffix = "nd";
  } else if (prize.prizeOrder === 3) {
    suffix = "rd";
  } else {
    suffix = "th";
  }

  return (
    <tr key={index}>
      <td>{prize.prizeOrder + suffix} Prize</td>
      <td>{prize.prizeNumber}</td>
    </tr>
  );
})}
        
      </table>
    </div>
  );
};

export default Counter;
